nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 40px;
    background-color: #000;
    height: auto;
    font-family: 'Bungee', cursive;
  }
  
  a {
    color: #ffffff;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    
  }
  
  ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #cur-sco, #top-sco {
      font-size: 2rem;
    }
  }


